import React, { useMemo, Suspense } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import { Provider, useSelector } from 'react-redux';
import { store, RootState } from './store';
import GlobalStyle from './styles/GlobalStyle'; //    
import { theme } from './styles/theme';
import Navigation from './components/Navigation';
import ProgressBar from './components/ProgressBar'; //    
// Solana Wallet Adapter imports
import { ConnectionProvider, WalletProvider } from '@solana/wallet-adapter-react';
import { WalletAdapterNetwork } from '@solana/wallet-adapter-base';
import { WalletModalProvider } from '@solana/wallet-adapter-react-ui';
import { clusterApiUrl } from '@solana/web3.js';
import { PhantomWalletAdapter, SolflareWalletAdapter } from '@solana/wallet-adapter-wallets';

// AppContext imports
import { AppProvider } from './context/AppContext';

// ErrorBoundary 
import ErrorBoundary from './components/ErrorBoundary';



// Lazy   ( )
const Home = React.lazy(() => import('./pages/Home'));
const Dashboard = React.lazy(() => import('./pages/Dashboard'));
const SquatChallenge = React.lazy(() => import('./pages/SquatChallenge'));
const ConnectWallet = React.lazy(() => import('./pages/ConnectWallet'));



// Default styles that can be overridden by your app
require('@solana/wallet-adapter-react-ui/styles.css');

//    Redux  ProgressBar  
const ProgressBarWrapper = () => {
  const { todayCount, dailyGoal } = useSelector((state: RootState) => state.squats);
  const progress = Math.min((todayCount / dailyGoal) * 100, 100); //  0 ~ 100%  
  return <ProgressBar progress={progress} />;
};

function App() {
  //  .env  Solana   (Devnet, Testnet, Mainnet  )
  const network = process.env.REACT_APP_SOLANA_NETWORK as WalletAdapterNetwork || WalletAdapterNetwork.Devnet;
  const endpoint = useMemo(() => clusterApiUrl(network), [network]);

  //      (Phantom, Solflare)
  const wallets = useMemo(() => [
    new PhantomWalletAdapter(),
    new SolflareWalletAdapter(),
  ], []);

  return (
    <ConnectionProvider endpoint={endpoint}>
      <WalletProvider wallets={wallets} autoConnect>
        <WalletModalProvider>
          <Provider store={store}>
            <AppProvider> {/* Context API  */}
              <ThemeProvider theme={theme}>
                <GlobalStyle />
                <Router>
                  <Navigation />
                  <ErrorBoundary>
                    <div style={{ textAlign: 'center', padding: '20px' }}>
                      <h1> Ex2Earn </h1>
                      <ProgressBarWrapper /> {/*    UI  */}
                    </div>
                    <Suspense fallback={<div>  ...</div>}>
                      <Routes>
                        <Route path="/" element={<Home />} />
                        <Route path="/dashboard" element={<Dashboard />} />
                        <Route path="/squat-challenge" element={<SquatChallenge />} />
                        <Route path="/connect-wallet" element={<ConnectWallet />} />
                      </Routes>
                    </Suspense>
                  </ErrorBoundary>
                </Router>
              </ThemeProvider>
            </AppProvider>
          </Provider>
        </WalletModalProvider>
      </WalletProvider>
    </ConnectionProvider>
  );
}

export default App;
